.s-about-video {
    padding-top: rem(110);

    @media screen and (max-width: 1099px) {
        padding-top: rem(89);
    }

    @media screen and (max-width: 991px) {
        padding-top: rem(36);
    }

    @media screen and (max-width: 767px) {
        padding-top: rem(44);
    }
    &__text {
        svg {
            display: block;
            width: auto;
            height: rem(146);

            @media screen and (max-width: 1699px) {
                height: rem(131);
            }

            @media screen and (max-width: 1099px) {
                height: rem(87);
            }

            @media screen and (max-width: 991px) {
                height: rem(78);
            }

            @media screen and (max-width: 767px) {
                height: rem(32);
            }
        }

        &:first-child {
            transform: translateX(-70px);

            @media screen and (max-width: 991px) {
                transform: translateX(0);
            }

            @media screen and (max-width: 767px) {
                margin-bottom: rem(11);
            }
        }

        &:last-child {
            transform: translateX(calc(50% - 457px));

            @media screen and (max-width: 1099px) {
                transform: translateX(calc(50% - 366px));
            }

            @media screen and (max-width: 991px) {
                transform: translateX(0);
            }
        }
    }

    &__iframe {
        position: relative;
        display: block;
        width: rem(915);
        height: rem(514);
        margin: 0 auto;
        background: #000;
        cursor: pointer;

        @media screen and (max-width: 1099px) {
            width: rem(732);
            height: rem(411);
        }

        @media screen and (max-width: 991px) {
            width: rem(708);
            height: rem(398);
        }

        @media screen and (max-width: 991px) {
            width: rem(708);
            height: rem(398);
        }

        @media screen and (max-width: 767px) {
            width: 100%;
            height: rem(197);
        }

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #000;
        }

        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: none;
        }

        &.play {
            &:after,
            .s-about-video__icon {
                display: none;
            }

            iframe {
                display: block;
            }
        }

        &:hover {
            .s-about-video__icon {
                opacity: 0.5;
            }
        }
    }

    &__icon {
        @include tr("all");
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;
        transform: translate(-50%, -50%);

        @media screen and (max-width: 767px) {
            width: rem(60);
        }
    }
}
