.s-company-materials {
    padding: rem(40) 0;

    @media (min-width: 1024px) {
        padding: rem(90) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(140) 0;
    }

    @media (min-width: 1920px) {
        padding: rem(148) 0;
    }

    &__title {
        margin-bottom: rem(30);
        font-size: rem(21);
        font-weight: 400;
        line-height: rem(25);
        text-align: center;
        color: #ceced0;

        @media (min-width: 768px) {
            margin-bottom: rem(45);
            font-size: rem(25);
            line-height: rem(29);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(62);
            font-size: rem(35);
            line-height: rem(41);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(71);
            font-size: rem(38);
            line-height: rem(45);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(75);
            font-size: rem(40);
            line-height: rem(47);
        }
    }

    &__items {
        display: grid;
        padding: 0 rem(30);
        grid-template-columns: repeat(1, 1fr);
        gap: rem(10);

        @media (min-width: 768px) {
            padding: 0 rem(67);
            grid-template-columns: repeat(2, 1fr);
            gap: rem(30);
        }

        @media (min-width: 992px) {
            padding: 0;
            grid-template-columns: repeat(3, 1fr);
            gap: rem(30);
        }

        @media (min-width: 1600px) {
            grid-template-columns: repeat(4, 1fr);
        }

        @media (min-width: 1920px) {
            gap: rem(30) rem(43);
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        height: rem(70);
        font-size: rem(13);
        line-height: rem(22);
        color: #ceced0;
        border: rem(2) solid;
        transition: all 0.5s ease-in-out;
        gap: rem(20);
        border-image-source: linear-gradient(
            124.81deg,
            #000000 -36.41%,
            #656669 117.15%
        );
        border-image-slice: 1;
        border-width: rem(2);

        @media (min-width: 768px) {
            height: rem(150);
            font-size: rem(17);
            line-height: rem(19);
        }

        @media (min-width: 1600px) {
            height: rem(213);
            font-size: rem(19);
            line-height: rem(32);
            gap: rem(32);
        }

        &:hover {
            color: white;
            border-color: white;
            border-image-source: none;
        }

        svg {
            width: rem(30);
            height: rem(30);

            @media (min-width: 1600px) {
                width: rem(40);
                height: rem(40);
            }
        }
    }
}
