@import "helpers/index";
@import "varibles/index";
@import "../blocks/modules/about/s_company_materials/s_company_materials";
@import "../blocks/modules/s_about_graviton/s_about_graviton";
@import "../blocks/modules/about/s_about_video/s_about_video";
@import "../blocks/modules/about/s_about_contacts/s_about_contacts";

.about-page {
    .main {
        position: relative;
        // background-color: #000203; // оставляем для дальнейших фонов, что бы не было претензий я придумал как

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            background-image: url(".././img/about/bg-page.png");
            background-size: contain;
            background-repeat: no-repeat;

            @media screen and (max-width: 767px) {
                height: 40%;
                background-size: cover;
            }
        }
    }

    .s-banner {
        overflow: hidden;

        &__title {
            max-width: rem(546);
            margin-bottom: rem(48);

            @media (max-width: 1600px) {
                margin-bottom: rem(0);
                padding-bottom: 134px;
            }

            @media (max-width: 1024px) {
                margin-bottom: rem(80);
                padding-bottom: 0;
            }

            @media (max-width: 991px) {
                max-width: rem(239);
                margin-bottom: rem(50);
            }

            @media (max-width: 768px) {
                margin-bottom: rem(90);
            }
        }
    }
}
