.s-about-graviton {
    position: relative;
    width: 100%;
    overflow: visible;
    padding: rem(60) 0;

    @media (min-width: 768px) {
        padding: rem(115) 0;
    }

    .title {
        position: relative;
        z-index: 0;
        margin-bottom: rem(45);
        text-align: center;
        color: #d9d9d91a;
        opacity: 0.6;
        letter-spacing: rem(26);

        @media (min-width: 768px) {
            text-align: left;
            letter-spacing: rem(16);
            font-size: rem(70);
        }

        @media (min-width: 1200px) {
            letter-spacing: rem(90);
            font-size: rem(110);
        }
        @media (min-width: 1600px) {
            z-index: -1;
            letter-spacing: 0.77em;
            font-size: rem(152);
        }
    }

    &__text {
        position: relative;
        z-index: 1;
        max-width: rem(704);
        margin-top: rem(300);
        margin-left: rem(20);
        margin-bottom: rem(50);

        @media (min-width: 768px) {
            margin-top: 0;
            margin-bottom: rem(250);
            margin-left: auto;
        }

        p {
            margin-bottom: rem(20);
            font-size: rem(14);
            font-weight: 400;
            line-height: 1.5;

            @media (min-width: 768px) {
                font-size: rem(20);
                font-weight: 500;
                line-height: 1.8;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__items {
        position: relative;
        z-index: 1;
    }

    &__item {
        position: relative;
        font-size: rem(12);
        font-weight: 300;
        line-height: 1.5;
        color: $grey_light_more_2;

        @media (min-width: 768px) {
            font-size: rem(20);
            line-height: 1.8;
        }

        &:first-child {
            margin-bottom: rem(40);
            margin-left: rem(20);
            padding-left: rem(20);

            @media (min-width: 768px) {
                max-width: rem(714);
                margin-right: rem(150);
                margin-bottom: rem(47);
                margin-left: rem(120);
                padding-left: 0;
            }

            @media (min-width: 992px) {
                margin-left: auto;
            }

            &:after {
                content: "";
                position: absolute;
                bottom: rem(-15);
                left: 0;
                width: rem(220);
                height: rem(28);
                padding-bottom: rem(23);
                padding-left: rem(40);
                border-bottom: rem(1) solid #8d8d8d;
                border-left: rem(1) solid #8d8d8d;

                @media (min-width: 768px) {
                    bottom: rem(-40);
                    left: rem(-40);
                }
            }

            &:before {
                content: "";
                position: absolute;
                z-index: 1;
                bottom: rem(-22);
                left: rem(-4);
                width: rem(10);
                height: rem(10);
                background: $red;
                border-radius: 100%;

                @media (min-width: 768px) {
                    bottom: rem(-44);
                    left: rem(-44);
                }
            }
        }

        &:last-child {
            margin-bottom: rem(40);
            margin-left: rem(20);
            padding-left: rem(20);

            @media (min-width: 768px) {
                max-width: rem(714);
                margin-top: rem(140);
                margin-left: rem(250);
                padding-left: 0;
            }

            &:after {
                content: "";
                position: absolute;
                bottom: rem(-15);
                left: 0;
                width: rem(220);
                height: rem(28);
                padding-bottom: rem(23);
                padding-left: rem(40);
                border-bottom: rem(1) solid #8d8d8d;
                border-left: rem(1) solid #8d8d8d;

                @media (min-width: 768px) {
                    bottom: rem(-40);
                    left: rem(-40);
                }
            }

            &:before {
                content: "";
                position: absolute;
                z-index: 1;
                bottom: rem(-22);
                left: rem(-4);
                width: rem(10);
                height: rem(10);
                background: $red;
                border-radius: 100%;

                @media (min-width: 768px) {
                    bottom: rem(-44);
                    left: rem(-44);
                }
            }
        }
    }

    &-page {
        position: relative;
        display: flex;
        flex-direction: column;
        min-height: 1186px;
        z-index: 1;
        overflow: visible;

        @media screen and (min-width: 1099px) {
            padding: 186px 0 0;
        }

        @media screen and (min-width: 768px) {
            padding: 136px 0 0;
        }

        @media screen and (max-width: 1099px) {
            min-height: 900px;
        }

        @media screen and (max-width: 991px) {
            min-height: 807px;
        }

        @media screen and (max-width: 767px) {
            min-height: 0;
        }

        .l-default {
            position: static;
        }

        .sphere-animation {
            position: static;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
        }

        .animation-wrapper {
            position: absolute;
            top: unset;
            bottom: 25px;
            left: 85px;
            z-index: -1;
            width: 731px;
            height: 731px;

            @media screen and (max-width: 1599px) {
                bottom: 0;
                left: -320px;
            }

            @media screen and (max-width: 1599px) {
                bottom: 100px;
                left: -100px;
                width: 598px;
                height: 598px;
            }

            @media screen and (max-width: 1099px) {
                bottom: 100px;
                left: -100px;
                width: 419px;
                height: 419px;
            }

            @media screen and (max-width: 991px) {
                left: -40px;
            }

            @media screen and (max-width: 767px) {
                position: static;
            }
        }

        .s-about-graviton {
            &__texts {
                display: flex;

                @media screen and (max-width: 1099px) {
                    flex-direction: column;
                }

                &-item {
                    width: 50%;

                    @media screen and (max-width: 1099px) {
                        width: 100%;
                    }

                    &:first-child {
                        margin-right: 114px;

                        @media screen and (max-width: 1699px) {
                            margin-right: 125px;
                        }

                        @media screen and (max-width: 1099px) {
                            max-width: 620px;
                            margin-right: 0;
                        }

                        p {
                            line-height: 180%;
                        }
                    }

                    &:last-child {
                        margin-top: 80px;

                        @media screen and (max-width: 1099px) {
                            width: 50%;
                            margin-top: 66px;
                            margin-left: auto;
                        }

                        @media screen and (max-width: 991px) {
                            margin-top: 57px;
                        }

                        @media screen and (max-width: 767px) {
                            width: 100%;
                            margin-top: rem(36);
                            margin-bottom: rem(60);
                        }
                    }
                }

                &-title {
                    font-size: rem(40);
                    line-height: 120%;
                    font-weight: 900;
                    color: #ceced0;
                    margin-bottom: rem(40);

                    @media screen and (max-width: 1699px) {
                        font-size: 38px;
                        margin-bottom: rem(47);
                    }

                    @media screen and (max-width: 1099px) {
                        font-size: 35px;
                        font-weight: 500;
                        margin-bottom: 20px;
                    }

                    @media screen and (max-width: 991px) {
                        font-size: 25px;
                        margin-bottom: 15px;
                    }

                    @media screen and (max-width: 767px) {
                        font-size: 19px;
                        margin-bottom: 20px;
                    }
                }

                p {
                    font-size: 19px;
                    font-weight: 300;
                    line-height: 120%;
                    color: #ceced0;

                    @media screen and (max-width: 991px) {
                        font-size: 15px;
                    }

                    @media screen and (max-width: 767px) {
                        font-size: 14px;
                    }

                    span {
                        font-weight: 900;
                    }
                }
            }

            &__bottom {
                position: static;
                z-index: 7;
                background: rgba(#4e5056, 0.5);
                margin-top: auto;
                padding: 54px 0;

                @media screen and (max-width: 1099px) {
                    padding: 32px 0;
                }

                &-wrapper {
                    display: flex;

                    @media screen and (max-width: 1099px) {
                        align-items: center;
                    }

                    @media screen and (max-width: 767px) {
                        align-items: flex-start;
                        flex-direction: column-reverse;
                    }
                }

                &-icon {
                    margin-left: auto;

                    @media screen and (max-width: 1099px) {
                        img {
                            display: block;
                            width: auto;
                            height: rem(50);
                        }
                    }

                    @media screen and (max-width: 767px) {
                        margin-left: 0;
                        margin-bottom: 15px;

                        img {
                            height: 30px;
                            max-height: 30px;
                        }
                    }
                }

                &-title {
                    width: 80%;
                    font-size: rem(40);
                    line-height: 120%;

                    @media screen and (max-width: 767px) {
                        width: 100%;
                        font-size: 15px;
                    }

                    @media screen and (max-width: 1699px) {
                        font-size: 35px;
                        margin-right: 43px;
                    }

                    @media screen and (max-width: 1099px) {
                        font-size: 30px;
                        margin-right: rem(40);
                    }

                    @media screen and (max-width: 991px) {
                        font-size: 21px;
                    }
                }
            }
        }

        .title-page {
            position: absolute;
            top: -140px;
            left: 50%;
            z-index: -1;
            transform: translateX(-50%);
            text-align: center;
            font-size: 400px;
            line-height: 1;
            font-weight: 900;
            letter-spacing: -0.03em;
            -webkit-text-fill-color: transparent;
            -webkit-text-stroke: 0.25rem rgba(255, 255, 255, 0.05);
            background-clip: text;
            text-fill-color: transparent;

            @media screen and (max-width: 1699px) {
                top: -200px;
                left: unset;
                transform: translateX(250px);
            }

            @media screen and (max-width: 1099px) {
                top: -120px;
                left: rem(60);
                font-size: 250px;
                transform: translateX(0);
            }

            @media screen and (max-width: 991px) {
                top: -100px;
                left: 20px;
                font-size: 200px;
            }

            @media screen and (max-width: 767px) {
                top: -50px;
                font-size: 95px;
            }
        }
    }
}
