.s-about-contacts {
    overflow: hidden;
    padding-bottom: rem(110);

    @media screen and (max-width: 1699px) {
        padding-bottom: 100px;
    }

    @media screen and (max-width: 1199px) {
        padding-bottom: 90px;
    }

    &__top {
        position: relative;
        display: flex;
        align-items: center;
        min-height: 350px;
        margin-bottom: 20px;

        @media screen and (max-width: 1699px) {
            min-height: 270px;
            margin-bottom: 41px;
        }

        @media screen and (max-width: 1199px) {
            flex-wrap: wrap;
            margin: 0 -15px;
            margin-bottom: 45px;
        }

        @media screen and (max-width: 600px) {
            align-items: flex-start;
            flex-direction: column;
            min-height: 0;
            margin: 0;
            margin-bottom: 20px;
        }

        .l-default {
            @media screen and (max-width: 1199px) {
                order: 1;
                display: flex;
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
                margin: 0;
            }
        }
    }

    &__title {
        font-size: rem(40);
        line-height: 1;
        margin-bottom: rem(18);

        @media screen and (max-width: 1699px) {
            font-size: 38px;
        }

        @media screen and (max-width: 1199px) {
            order: 1;
            width: calc(50% - 30px);
            font-size: 30px;
            margin: 0 15px;
        }

        @media screen and (max-width: 600px) {
            width: 100%;
            font-size: 21px;
            margin: 0;
            margin-bottom: 9px;
        }
    }

    &__subtitle {
        max-width: 558px;
        font-size: 21px;
        line-height: 120%;

        @media screen and (max-width: 1699px) {
            max-width: 494px;
            font-size: 19px;
        }

        @media screen and (max-width: 1199px) {
            order: 1;
            width: calc(50% - 30px);
            font-size: 15px;
            margin: 0 15px;
        }

        @media screen and (max-width: 600px) {
            width: 100%;
            font-size: 13px;
            margin: 0;
        }
    }

    &__text {
        font-family: "Roboto";
        position: absolute;
        top: 50%;
        right: 0;
        z-index: -1;
        transform: translateY(-50%) translateX(120px);
        opacity: 0.1;
        font-size: 350px;
        font-weight: 500;
        letter-spacing: -0.05em;
        white-space: nowrap;
        color: #4e5056;
        line-height: 1;

        @media screen and (max-width: 1699px) {
            font-size: 300px;
            transform: translateY(-50%) translateX(0);
        }

        @media screen and (max-width: 1199px) {
            top: unset;
            order: 0;
            position: static;
            transform: translateY(0) translateX(0);
            font-size: 200px;
            margin: 0 15px;
            margin-left: auto;
            margin-bottom: 17px;
        }

        @media screen and (max-width: 600px) {
            font-size: 150px;
            line-height: 0.6;
            white-space: pre-wrap;
            transform: translateY(75px);
        }
    }

    .form {
        @media screen and (max-width: 1199px) {
            margin: 0 -15px;
        }

        @media screen and (max-width: 600px) {
            margin: 0;
        }

        &__inputs {
            display: flex;
            flex-wrap: wrap;
        }

        &__columns {
            display: flex;
            margin-bottom: rem(40);

            @media screen and (max-width: 1199px) {
                margin-bottom: 0;
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }

        &__textarea {
            min-height: 100%;
            margin-bottom: 0;

            textarea {
                flex: 1;

                @media screen and (max-width: 1199px) {
                    max-height: 289px;
                }

                @media screen and (max-width: 600px) {
                    max-height: none;
                    min-height: 132px;
                }
            }
        }

        .btn {
            display: flex;
            max-width: 100%;
            font-size: 15px;
            line-height: 1;
            border-radius: 5px;
            background: #333333;

            &:hover {
                opacity: 0.4;
            }
        }

        &__submit {
            display: flex;
            flex-direction: column;

            @media screen and (max-width: 600px) {
                margin-bottom: 0 !important;
            }
        }

        label {
            margin-bottom: rem(11);
        }

        &__column {
            @media screen and (max-width: 1199px) {
                width: calc(50% - 30px);
                margin: 0 15px;
            }

            &:first-child {
                width: 100%;
                max-width: 493px;
                margin-right: 96px;

                @media screen and (max-width: 1699px) {
                    max-width: 465px;
                    margin-right: 42px;
                }

                @media screen and (max-width: 1199px) {
                    width: calc(50% - 30px);
                    margin: 0 15px;
                }

                @media screen and (max-width: 600px) {
                    width: 100%;
                    max-width: 100%;
                    margin: 0;
                    margin-bottom: 14px;
                }
            }

            &:last-child {
                display: flex;
                flex-wrap: wrap;
                margin: 0 -45px;

                @media screen and (max-width: 1699px) {
                    margin: 0 -20px;
                }

                @media screen and (max-width: 1199px) {
                    margin: 0 15px;
                }

                @media screen and (max-width: 600px) {
                    width: 100%;
                    margin: 0;
                    margin-bottom: 22px;
                }

                .form {
                    &__input,
                    &__submit {
                        width: calc(50% - 90px);
                        margin: 0 45px;
                        margin-bottom: 34px;

                        @media screen and (max-width: 1699px) {
                            width: calc(50% - 40px);
                            margin: 0 20px 34px;
                        }

                        @media screen and (max-width: 1199px) {
                            width: 100%;
                            margin: 0 0 14px;
                        }

                        &:last-child,
                        &:nth-last-child(-n + 2),
                        .btn {
                            margin-bottom: 0;

                            @media screen and (max-width: 1199px) {
                                margin-bottom: 17px;
                            }

                            @media screen and (max-width: 600px) {
                                margin-bottom: 22px;
                            }
                        }

                        input,
                        button {
                            width: 100%;
                            min-height: 48px;
                        }

                        .btn {
                            @media screen and (max-width: 600px) {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            }
        }

        &__checkbox {
            @media screen and (max-width: 1199px) {
                align-items: center;
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }

            &-text {
                @media screen and (max-width: 1199px) {
                    font-size: 12px;
                }
            }

            &-btn {
                margin-right: 20px;
            }
        }
    }
}
